.slinky-menu {
    overflow: hidden;
    transform: translateZ(0); }
    .slinky-menu > ul {
      left: 0;
      position: relative;
      transform: translateZ(0); }
    .slinky-menu ul,
    .slinky-menu li {
      list-style: none;
      margin: 0; }
    .slinky-menu ul {
      width: 100%; }
    .slinky-menu a {
      align-items: center;
      display: flex; }
      .slinky-menu a span {
        flex: 1;
        line-height: 1.4; }
    .slinky-menu li ul {
      display: none;
      left: 100%;
      position: absolute;
      top: 0; }
    .slinky-menu .header {
      display: flex; }
      .slinky-menu .header .title {
